import React, { Component } from 'react';
//import styled, { keyframes } from 'styled-components';
//import { bounceInRight } from 'react-animations';
import ScrollAnimation from 'react-animate-on-scroll';

import './about.scss';
import imgMy from './img/port-img.jpeg';
import desing from './img/design.svg';
import mobile from './img/mobile.svg';
import web from './img/WEB.svg';

//const ScrollAnimation = styled.div`animation: 2s ${keyframes`${bounceInRight}`}`;

export default class About extends Component {
    render() {
        return (

            <section className="about" id="#about">
                <div className="content about-content__size">
                <ScrollAnimation animateIn='animate__fadeInRight' className="animate__delay-4s">
                    <div className="about__wrapper">
                        <div className="about__photo">
                            <div className="about__photo-border"></div>
                            <img src={imgMy} alt="photo"></img>
                            
                        </div>
                        <div className="about__descr">
                            <h2 className="title title__fz16 about__title">About me</h2>
                            <div className="title title__fz36 about__subtitle">I'm Olga</div>
                            <div className="divider"></div>
                            <p className="about__text"> 
                                Hi everyone! This is my site - portfolio.
                                In which I will tell you about my skills and what I use in my work to create a high-quality WEB product.
                                I'm <span>Frontend</span> developer. My work experience is 3 years on different projects.
                                I want to improve myself professionally, develop high-quality projects and bring benefits to this world.
                            </p>
                        </div>
                        <div className="about__skills">
                            <div className="about__skills-item">
                                <div className="about__skills-circle">
                                    <img src={web}></img>
                                </div>
                                <div>
                                    <div className="title title__fz14">
                                        Frontend developer
                                    </div>
                                    <div className="about__skills-text">
                                    Designing the user interface and functionality that run on the client side of a website or application.
                                        This is everything that the user sees when opening a web page and with which he interacts.
                                    </div>
                                </div>
                            </div>
                            <div className="about__skills-item">
                                <div className="about__skills-circle">
                                    <img src={desing}></img>
                                </div>
                                <div>
                                    <div className="title title__fz14">
                                        Website support
                                    </div>
                                    <div className="about__skills-text">
                                        Optimization of the finished product, refinement of user interface details
                                    </div>
                                </div>
                            </div>
                            <div className="about__skills-item">
                                <div className="about__skills-circle">
                                    <img src={mobile}></img>
                                </div>
                                <div>
                                    <div className="title title__fz14">
                                        Software QA Engineer
                                    </div>
                                    <div className="about__skills-text">
                                        Quality Assurance - checking and fixing bugs, writing test-cases and bug reports
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    </ScrollAnimation>
                </div>
            </section>
        );
    }
}