import React, { Component } from 'react';
import styled, { keyframes } from 'styled-components';
import { bounceInLeft } from 'react-animations';

import './promo.scss';

//import imgBackg from './img/background.jpg';

const Bounce = styled.div`animation: 2s ${keyframes`${bounceInLeft}`}`;


export default class Promo extends Component {
    render() {
      return (
        <section className="promo">
          <div className="content">
          <div className="promo-overlay"></div>
          <Bounce>
            <div className="promo-wrapper">
                <div className="promo__style promo__subtitle f-Pop">My name is Olga Garder</div>
                <h1 className="promo__title f-Pop">Welcome to my portfolio site I'm a frontend developer</h1>

                <div className="promo__btns">
                  <a className="promo__link promo__btn main-color f-Pop" href="https://drive.google.com/file/d/1IST52-ltS0ZqUGPN6zYItdJtkG-SpYgy/view?usp=sharing">Resume En</a>
                  <a className="promo__link f-Pop" href="https://drive.google.com/file/d/1kWbHlZeXz_VRK1PgAoXcP9xjAp6YRk8A/view?usp=sharing">Resume Ru</a>
                </div>
            </div>
            </Bounce>
          </div>
        </section>
      );
    }
}
