import React, { Component } from 'react';

import * as Scroll from 'react-scroll';
import { Link, Button, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'
import './footer.scss';

import fa from './img/fac.svg';
import inst from './img/inst.svg';
import vk from './img/vk.svg';

export default class Footer extends Component {
    render() {
        return (
            <section className="footer">
                <div className="content">

                    <div className="footer-d__flex">
                        <div className="footer__social-group">
                            <a className="footer__social-item">
                                <img src={fa} alt="icon"></img>
                            </a>
                            <a className="footer__social-item">
                                <img src={inst} alt="icon"></img>
                            </a>
                            <a className="footer__social-item">
                                <img src={vk} alt="icon"></img>
                            </a>					
                        </div>
                        <ul className="footer-items">
                            <li><a className="footer-item" href="mailto:garderolga@yahoo.com">garderolga@yahoo.com</a></li>
                        </ul>
                        <div className="footer-logo">
                            Olga Garder
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}