import React, { Component } from 'react';

import ScrollAnimation from 'react-animate-on-scroll';
import Carousel from 'react-bootstrap/Carousel';

import './lookbus.scss';

import lookbus from './img/lookbus.jpg';
import bug2 from './img/bug-2.jpg';
import bug3 from './img/bug-3.jpg';
import bug4 from './img/bug-4.jpg';
import bug6 from './img/bug-6.jpg';

export default class Lookbus extends Component {
    render() {
        return (
            <section className="lookbus">
                <div className="content">
                    <ScrollAnimation animateIn='animate__fadeIn' className="animate__delay-2s">

                        <div className="title title__fz36 title__section-subtitle">Testing App <a className="brief-go_to" href="https://lookingbus.com/"> LookingBus</a></div>
                        <div className="lookbus-wrapper">
                            <div className="lookbus-left">
                                <Carousel fade variant="dark">
                                    <Carousel.Item>
                                        <img
                                        className="d-block w-100"
                                        src={lookbus}
                                        alt="First slide"
                                        />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img
                                        className="d-block w-100"
                                        src={bug2}
                                        alt="Second slide"
                                        />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img
                                        className="d-block w-100"
                                        src={bug3}
                                        alt="Third slide"
                                        />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img
                                        className="d-block w-100"
                                        src={bug4}
                                        alt="Third slide"
                                        />
                                    </Carousel.Item>
                                    <Carousel.Item>
                                        <img
                                        className="d-block w-100"
                                        src={bug6}
                                        alt="Third slide"
                                        />
                                    </Carousel.Item>
                                </Carousel>
                            </div>
                            <div className="lookbus-right">
                                <p>
                                    I'm working on a project "Looking Bus". Looking Bus is the project for people with  disabilities. 
                                    I tested App in the different roles. I tried to create an account, reserved transports, canceled reservation, changed the profile for different disability groups. 
                                    I found some bugs in the App, I reported founded bugs in the Jira. The Looking Bus is very good project, 
                                    I think so this's the project of the future, for a more comfortable life for any person with disabilities. 
                                    Of course for beginners testers it's a very good practice. 
                                    TK: that would be prefect for the internship interview but might be a bit too 'student' for the job.
                                    My projects was creating android emulator, install, verify bug fixes on  Looking Bus.apk
                                </p>
                                <div className="lookbus-links">
                                    <a href="https://docs.google.com/document/d/1iJ1fcDXgMAsD24a6dwecYnV_ZZIiGjMmZfQ-zX7Rta8/edit#">Handouts</a>
                                </div>
                            </div>
                        </div>
                    </ScrollAnimation>
                </div>
            </section>
        );
    }
}