import React, { Component } from 'react';
import ScrollAnimation from 'react-animate-on-scroll';
import ProgressLine from "../progressLine";

import './animateSkills.scss';

export default class AnimateSkills extends Component {
    render() {
        return (
            <section className="skills-animate">
                <div className="content">
                <ScrollAnimation animateIn='animate__fadeInUp' className="animate__delay-4s">
                    <div class="skills__ratings">
                        <div className="skills__ratings-item">
                            <div className="title title__fz14 skills__ratings-title">HTML5</div>
                            <div className="skills__ratings-counter">100%</div>
                            <ProgressLine
                                 backgroundColor="#E8E4DE"
                                 visualParts={[
                                   {
                                     percentage: "100%",
                                     color: "#FFA501"
                                   }
                                ]}
                            />
                        </div>
                        <div class="skills__ratings-item">
                            <div className="title title__fz14 skills__ratings-title">JavaScript</div>
                            <div className="skills__ratings-counter">80%</div>
                            <ProgressLine
                                 backgroundColor="#E8E4DE"
                                 visualParts={[
                                   {
                                     percentage: "80%",
                                     color: "#FFA501"
                                   }
                                ]}
                            />
                        </div>
                        <div className="skills__ratings-item">
                            <div className="title title__fz14 skills__ratings-title">Css3 Sass Less</div>
                            <div className="skills__ratings-counter">100%</div>
                            <ProgressLine
                                 backgroundColor="#E8E4DE"
                                 visualParts={[
                                   {
                                     percentage: "100%",
                                     color: "#FFA501"
                                   }
                                ]}
                            />
                        </div>
                        <div className="skills__ratings-item">
                            <div className="title title__fz14 skills__ratings-title">jQuery</div>
                            <div className="skills__ratings-counter">75%</div>
                            <ProgressLine
                                 backgroundColor="#E8E4DE"
                                 visualParts={[
                                   {
                                     percentage: "75%",
                                     color: "#FFA501"
                                   }
                                ]}
                            />
                        </div>
                        <div className="skills__ratings-item">
                            <div className="title title__fz14 skills__ratings-title">Bootstrap, UIkit</div>
                            <div className="skills__ratings-counter">90%</div>
                            <ProgressLine
                                 backgroundColor="#E8E4DE"
                                 visualParts={[
                                   {
                                     percentage: "90%",
                                     color: "#FFA501"
                                   }
                                ]}
                            />
                        </div>
                        <div className="skills__ratings-item">
                            <div className="title title__fz14 skills__ratings-title">React - Redux</div>
                            <div className="skills__ratings-counter">50%</div>
                            <ProgressLine
                                 backgroundColor="#E8E4DE"
                                 visualParts={[
                                   {
                                     percentage: "50%",
                                     color: "#FFA501"
                                   }
                                ]}
                            />
                        </div>
                    </div>
                </ScrollAnimation>
                </div>
            </section>
        );
    }
}