import React , {useState} from 'react';
import { Navbar, Nav, Button, Modal , Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';

import './navbar.scss';

export default function Navibar() {

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    return (
        <section className="navbars">
            <div className="content">
            <Navbar collapseOnSelect expand="lg" bg="#white" variant="dark">
                <Navbar.Brand className="navbars-logo">Portfolio</Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mr-auto">
                        <ul className="navbars__btns">
                            <li className="navbars__btns-first"><Link to="/" className="navbars-link">Frontend developer</Link></li>
                            <li className="navbars__btns-last"><Link to="/qa" className="navbars-link">Softweare QA</Link></li>
                        </ul>
                    </Nav>
                    <Nav>
                        <Button className="navbars-btn mr-2" onClick={handleShow}>Contact me</Button>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Contact me</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Email Adress</Form.Label>
                            <Form.Text className="text-muted">garderolga@yahoo.com</Form.Text>
                        </Form.Group>
                        <Form.Group controlId="formBasicPassword">
                            <Form.Label>Telegram phone</Form.Label>
                            <Form.Text className="text-muted">+7 (988) 995 -79-57</Form.Text>
                        </Form.Group>
                    </Form>
                </Modal.Body>
            </Modal>
            </div>
        </section>
    )
}