import React, { Component } from 'react';
import { Tabs , Tab} from 'react-bootstrap';

import './qaresume.scss';

//icons
import courses from './img/courses.svg';
import developer from './img/developer.svg';

//img

import html from './img/skills/html5.svg';
import css from './img/skills/css3.svg';
import javasc from './img/skills/js.svg';
import jq from './img/skills/jquery.png';
import exal from './img/skills/exal.png';
import jira from './img/skills/jira.jpg';
import selenium from './img/skills/selenium.png';
import sql from './img/skills/sql.jpg';
import mysql from './img/skills/mysql.jpg';
import linux from './img/skills/linux.png';
import postman from './img/skills/postman.png';
import sdk from './img/skills/sdk.png';

export default class QaResume extends Component {
    render() {
        return (
            <section className="qa-resume">
                <div className="content">

                    <h2 className="title title__fz16 title__section-title">My experience</h2>
                    <div className="title title__fz36 title__section-subtitle">What do I use in my work? </div>
                    <div className="divider"></div>

                    <Tabs defaultActiveKey="home" id="uncontrolled-tab-example">
                        <Tab eventKey="home" title="Education / Work">
                            <div className="resume__wrapper">
                                <div className="resume__column">
                                    <h3 className="title title__fz20 resume__column-title">Education</h3>
                                    <ul>
                                        <li>
                                            <div className="resume__item">
                                                <div className="resume__item-head">
                                                    <div className="resume__item-icon">
                                                        <img src={courses}></img>
                                                    </div>
                                                    <h4 className="title title__fz14">Portnov Computer School</h4>
                                                    <div className="resume__item-location"> Courses | Los Altos CA, USA</div>
                                                </div>
                                                <div className="resume__item-body">
                                                    Portnov Computer School is a private institution, which is approved to operate by the Bureau for Private Postsecondary Education.
                                                    Approval to operate means compliance with state standards as set forth in the California Educational Code.
                                                </div>
                                            </div>
                                        </li>
                                    </ul>
                                </div>
                            
                                <div className="resume__column">
                                    <h3 className="title title__fz20 resume__column-title">Work experience</h3>
                                    <ul>
                                        <li>
                                            <div className="resume__item">
                                                <div className="resume__item-head">
                                                    <div className="resume__item-icon">
                                                        <img src={ developer}></img>
                                                    </div>
                                                    <h4 className="title title__fz14">Software QA</h4>
                                                    <div className="resume__item-location">uTest</div>
                                                </div>
                                                <div className="resume__item-body">
                                                    Tested the Social network application, web and mobile versions, tested Location tracker application
                                                </div>
                                            </div>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </Tab>
                        <Tab eventKey="skills" title="Knowledge / Skills">
                            <div className="skills">
                                <div className="skills__items">
                                    <div className="skills__item">
                                        <div className="skills__item-img">
                                            <img src={exal} alt="html5"></img>
                                        </div>
                                        <h3 className="title title__fz14">Test Case (Bug Report)</h3>
                                        <p>Writing test cases and bug reports.</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img w-100">
                                            <img src={jira} alt="html5"></img>
                                        </div>
                                        <h3 className="title title__fz14">Jira (Agile)</h3>
                                        <p>Writing bug reports to the Jira system, tracking the project backlog.</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img">
                                            <img src={html} alt="html5"></img>
                                        </div>
                                        <h3 className="title title__fz14">HTML5</h3>
                                        <p>Knowledge of HTML5. Experience in spreadsheet layout for e-mail. In my projects I use the semantics.</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img">
                                            <img src={css} alt="css3"></img>
                                        </div>
                                        <h3 className="title title__fz14">CSS3</h3>
                                        <p>In my projects I practice preprocessors (Scss, Sass, Less) and also use Css animations</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img">
                                            <img src={javasc} alt="java script"></img>
                                        </div>
                                        <h3 className="title title__fz14">JavaScript</h3>
                                        <p>This programming language allows you to bring anything to life: sliders, windows, tooltips, tabs, getting data from the server and much more.</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img">
                                            <img src={jq} alt="Jquery"></img>
                                        </div>
                                        <h3 className="title title__fz14">Jquery</h3>
                                        <p> Many projects use the Jquery library to speed up development. We will not integrate it into the project without the need to integrate it, but the skill of working with it is present.</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img w-100">
                                            <img src={selenium} alt="Node.js"></img>
                                        </div>
                                        <h3 className="title title__fz14">Selenium / Python</h3>
                                        <p>Basic knowledge. Creating a small backend part: routing, form processing</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img w-50">
                                            <img src={sql} alt="Node.js"></img>
                                        </div>
                                        <h3 className="title title__fz14">SQL</h3>
                                        <p>Basic knowledge.Knowledge of sql commands (queries) for queries in database</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img w-50">
                                            <img src={mysql} alt="Node.js"></img>
                                        </div>
                                        <h3 className="title title__fz14">MySQL</h3>
                                        <p>Basic knowledge. Database testing experience</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img w-50">
                                            <img src={linux} alt="Node.js"></img>
                                        </div>
                                        <h3 className="title title__fz14">Lunix</h3>
                                        <p>Knowledge of commands and work in the terminal of a Linux machine. Backend testing experience</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img w-50">
                                            <img src={postman} alt="Node.js"></img>
                                        </div>
                                        <h3 className="title title__fz14">Postman</h3>
                                        <p>Experience of testing Api part using Postman tool</p>
                                    </div>
                                    <div className="skills__item">
                                        <div className="skills__item-img">
                                            <img src={sdk} alt="Node.js"></img>
                                        </div>
                                        <h3 className="title title__fz14">Android Studio</h3>
                                        <p>Experience in testing the operating system, testing different versions of mobile devices, testing mobile applications in the emulator, installing apk</p>
                                    </div>
                                </div>
                            </div>
                        </Tab>
                    </Tabs>
                </div>
            </section>
        );
    }
}