import Navibar from '../navbar';
import QaResume from '../qa-resume/qaresume';
import Brief from '../brief/brief';
import Lookbus from '../lookbus/lookbus';
import Footer from '../footer';

export default class Softweare extends Component {
    render() {
        return (
            <>
                <Navibar/>
                <Qapromo/>
                <QaResume/>
                <Brief/>
                <Lookbus/>
                <Footer/>
            </>
        );
    }
}
