import React, { Component } from 'react';
import './qapromo.scss';

import styled, { keyframes } from 'styled-components';
import { rollIn } from 'react-animations';

const Bounce = styled.div`animation: 2s ${keyframes`${rollIn}`}`;

export default class Qapromo extends Component {
    render() {
        return (
            <section className="qa-promo">
                <div className="content">
                    <Bounce>
                        <div className="promo-wrapper">
                            <div className="promo__style promo__subtitle f-Pop">My name is Olga Garder</div>
                            <h1 className="promo__title f-Pop">Welcome to my portfolio site I'm a Softweare QA Engineer</h1>

                            <div className="promo__btns">
                            <a className="promo__link promo__btn main-color f-Pop" href="#ffg">Resume En</a>
                            <a className="promo__link f-Pop" href="#gffgfg">Resume Ru</a>
                            </div>
                        </div>
                    </Bounce>
                </div>
            </section>
        );
    }
}